<template>
  <div class="box">
    <Navbar>
      <template #go>
        <van-icon name="arrow-left" />
      </template>
      <template #title>
        会员登录
      </template>
    </Navbar>
    <div class="from">
      <div class="tips">
        <h2>手机号登录</h2>
        <p>未注册的手机号登录后将自动注册</p>
      </div>
      <div class="controller">
        <div><input type="text" placeholder="请输入手机号码" v-model="form.mobile"></div>
        <div class="border">
          <input type="text" placeholder="请输入图形验证码" v-model="form.captchaCode">
          <img :src="imgUrl" alt="" @click="changeImgUrl">
        </div>
        <div class="border">
          <input type="text" placeholder="请输入短信验证码" v-model="smsCode">
          <span @click="getCaptcha" v-if="bool">获取验证码</span>
          <span v-else @click="$toast('请等待倒计时完成后发送')" style="color: black;">{{backNum}}后重新发送</span>
        </div>
      </div>
      <div class="button">
        <button @click="login">登录</button>
      </div>
    </div>
  </div>

</template>

<script>
import Navbar from '@/components/Navbar.vue'
import { Toast } from 'vant'
import Login from '@/api/login'
import catpathc from '@/api/catpathc'
export default {
  name: 'loginIndex',
  components: {
    Navbar
  },
  async created () {
    this.changeImgUrl()
  },
  data () {
    return {
      imgUrl: '',
      form: {
        captchaCode: '',
        captchaKey: '',
        mobile: ''
      },
      smsCode: '',
      bool: true,
      backNum: 60,
      clearS: ''
    }
  },
  methods: {
    async changeImgUrl () {
      const res = await Login.changeImgUrl()
      this.imgUrl = res.data.base64
      this.form.captchaKey = res.data.key
    },
    //* **********************************************************************************************************************
    async getCaptcha () {
      if (!this.voidFn()) return
      // 发起请求

      Toast('发送成功,请注意查收')
      Toast('验证码为246810')
      this.bool = false

      this.clearS = setInterval(() => {
        if (this.backNum <= 0) {
          clearInterval(this.clearS)
          this.bool = true
          this.backNum = 60
        }
        this.backNum--
      }, 1000)

      await catpathc.getCaptcha(this.form.captchaCode, this.form.captchaKey, this.form.mobile)
    },
    //* **********************************************************************************************************************
    async login () {
      if (!this.voidFn()) return
      if (!/^\d{6}$/.test(this.smsCode)) {
        Toast('请输入正确的验证码')
        return
      }
      const res = await Login.login({ form: { smsCode: this.smsCode, mobile: this.form.mobile, isParty: false, partyData: {} } })
      Toast('登录成功')
      // 将token存入vuex
      this.$store.commit('user/cmUserInfo', res.data)
      // 返回上一页
      console.log(this.$store.state.loginToUrl)
      this.$router.replace(this.$store.state.loginToUrl)
    },
    voidFn () {
      if (!/^1[2-9]\d{9}$/.test(this.form.mobile)) {
        Toast('请输入正确的手机号')
        return false
      } else if (!/^\w{4}$/.test(this.form.captchaCode)) {
        Toast('请输入正确的图形验证码')
        return false
      }
      return true
    }
  },
  beforeDestroy () {
    clearInterval(this.clearS)
    this.bool = true
    this.backNum = 60
  }
}
</script>

<style lang="less" scoped>
.from {
  overflow: auto;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 23px;
  padding-top: 50px;
  background: #fff;
  .tips {
    h2 {
      font-size: 27px;
      font-weight: 400;
      margin-bottom: 10px;
    }
    p {
      color: grey;
      font-size: 15px;
    }
  }
  .controller {
    margin-top:40px;
    padding: 0 10px;
    div {
      height: 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      input {
        outline: none;
        border: 0;
        font-size: 15px;
      }
      img {
        width: 110px;
      }
      span {
        margin-right: 10px;
        font-size: 13px;
        color: #faa927;
      }
    }
  }
  .button {
    button {
      margin-top: 30px;
      width: 100%;
      height: 50px;
      border-radius: 25px;
      border: 0;
      background: linear-gradient(to right, #f6b138, #ff960d);
      font-size: 18px;
      color: #fff;
    }
  }
}
.border {
  border-bottom: 1px solid #ececec;
}

</style>
