// 封装所有login有关的axios请求，请求与页面相分离
import request from '@/utils/request'
class Login {
  // 获取图片验证码
  changeImgUrl () {
    return request.get('/captcha/image')
  }

  postRq (url, obj) {
    return request.post(url, obj)
  }

  //   登录按钮
  login (obj) {
    return this.postRq('/passport/login', obj)
  }
}

export default new Login()
