import axios from 'axios'
import { Toast } from 'vant'
const captcha = axios.create({
  baseURL: 'http://smart-shop.itheima.net/index.php?s=/api',
  timeout: 5000
})

// 添加请求拦截器
captcha.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  config.headers.platform = 'H5'
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 添加响应拦截器
captcha.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么
  const res = response.data
  if (res.message === '很抱歉，图形验证码不正确') {
    Toast(res.message)
    return Promise.reject(res.message)
  }
  return res
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  return Promise.reject(error)
})

class Captcha {
  // 获取短信验证码
  getCaptcha (captchaCode, captchaKey, mobile) {
    return captcha.post('/captcha/sendSmsCaptcha', { form: { captchaCode, captchaKey, mobile } })
  }
}

export default new Captcha()
